@font-face {
  font-family: "Avenir";
  src: url("data:font/opentype;base64,");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: "Avenir";
  src: url("data:font/opentype;base64,");
  font-style: normal;
  font-weight: 600;
}
body {
  margin: 0;
  font-family: Avenir;
}
.container {
  padding: 0 10px;
  margin: 0 auto;
  width: 960px;
  font-weight: 400;
}
.main-text {
  font-size: 16px;
  font-weight: normal;
  color: #fffefe;
  text-decoration: none;
  font-family: Avenir;
}
.main-text--mini {
  text-transform: uppercase;
  font-size: 11px;
}
.main-text--big {
  font-size: 18px;
}
.main-text--black {
  color: #262626;
}
.main-text--title {
  color: #262626;
  font-size: 46px;
}
/*
LESS variables are information about icon's compiled state, stored under its original file name

.icon-home {
  width: @icon-home-width;
}

The large array-like variables contain all information about a single icon
@icon-home: x y offset_x offset_y width height total_width total_height image_path name;

At the bottom of this section, we provide information about the spritesheet itself
@spritesheet: width height image @spritesheet-sprites;
*/
/*
The provided classes are intended to be used with the array-like variables

.icon-home {
  .sprite-width(@icon-home);
}
.icon-email {
  .sprite(@icon-email);
}

Example usage in HTML:

`display: block` sprite:
<div class="icon-home"></div>

To change `display` (e.g. `display: inline-block;`), we suggest using a common CSS class:

// CSS
.icon {
  display: inline-block;
}

// HTML
<i class="icon icon-home"></i>
*/
/*
The `.sprites` mixin generates identical output to the CSS template
  but can be overridden inside of LESS

This must be run when you have at least 2 sprites.
  If run with a single sprite, then there will be reference errors.

.sprites(@spritesheet-sprites);
*/
.page-header {
  display: flex;
  flex-direction: column;
}
.page-header__inner {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.logo {
  display: flex;
  flex-direction: column;
  text-decoration: none;
  justify-content: flex-start;
  margin-left: 11px;
  margin-top: 4px;
}
.logo:hover {
  opacity: .7;
  transition: ease-out 0.25s;
}
.logo__img {
  width: 90%;
}
.logo__text {
  color: #fffefe;
  text-decoration: none;
  font-size: 14px;
  font-weight: 400;
  font-family: Avenir;
  padding-top: 8px;
}
.promo__wrapper {
  background-image: url('../img/bgr.png');
  min-height: 795px;
  background-repeat: no-repeat;
  background-size: cover;
}
.promo__slogan {
  font-family: Avenir;
  font-weight: 400;
  margin-top: 243px;
  margin-bottom: 0;
  font-size: 72px;
  text-align: center;
  color: #fffefe;
  line-height: 67px;
  letter-spacing: 1px;
}
.promo__text {
  color: #fffefe;
  margin: 0;
  font-size: 26px;
  margin-top: 31px;
  font-weight: normal;
}
.promo__btn {
  margin-top: 43px;
}
.promo__inner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.main-nav {
  display: flex;
  flex-direction: row;
  flex-basis: 58%;
  align-items: baseline;
  justify-content: space-between;
  font-size: 16px;
}
.lang {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  padding-top: 18px;
  padding-bottom: 1px;
  padding-right: 11px;
}
.lang__item {
  padding-left: 22px;
}
.lang__item:hover {
  opacity: .7;
  transition: ease-out 0.25s;
}
.nav {
  background-color: transparent;
  border: 0;
  font-size: 16px;
  color: #fffefe;
}
.nav__item {
  position: relative;
  margin-left: 11px;
}
.nav__item:after {
  content: '';
  display: inline-block;
  background-image: url(../img/sprite-039459358558591373.png);
  background-position: 0px 0px;
  width: 8px;
  height: 6px;
  position: absolute;
  right: -17px;
  top: 8px;
}
.nav__item:hover {
  opacity: .7;
  transition: ease-out 0.25s;
}
.login {
  flex-basis: 40%;
  padding: 10px 0px;
  border: 1px solid #b8b1ac;
  border-radius: 25px;
  text-align: center;
}
.login__item {
  display: inline-block;
  font-size: 16px;
  line-height: 26px;
  color: #fffefe;
  text-decoration: none;
}
.login__item:hover {
  opacity: .7;
  transition: ease-out 0.25s;
}
.login__item:first-of-type {
  border-right: 1px solid #b8b1ac;
  padding-right: 17px;
}
.login__item:last-of-type {
  padding-left: 17px;
}
.btn {
  width: 234px;
  height: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  color: #fffefe;
  text-decoration: none;
  background-color: #2856b3;
  border-radius: 30px;
}
.btn:hover {
  background-color: #2593b2;
  transition: ease-out 0.25s;
}
.info {
  background-image: url('../img/guy.png');
  background-repeat: no-repeat;
  background-size: 35%;
  background-position: 107px 71px;
  padding-bottom: 128px;
}
.info__inner {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}
.info__wrapper {
  flex-basis: 57%;
}
.info__title {
  margin-top: 89px;
  margin-bottom: 32px;
}
.info__list {
  margin-top: 25px;
  margin-bottom: 25px;
  padding-left: 27px;
}
.info__item {
  list-style-type: none;
  position: relative;
  line-height: 30px;
}
.info__item:before {
  content: '';
  display: inline-block;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background-color: #0f4ed0;
  position: absolute;
  left: -19px;
  top: 11px;
}
.info__btn {
  margin-top: 41px;
  margin-bottom: 58px;
}
.info__link {
  font-size: 14px;
  line-height: 15px;
  margin: 0;
  text-transform: uppercase;
  color: #b2b2b2;
  box-sizing: border-box;
  letter-spacing: 1px;
}
.info__link:first-of-type {
  margin-bottom: 7px;
}
.info__link-wrapper {
  width: 72%;
  padding: 30px;
  border: 1px dashed #b2b2b2;
}
.page-footer__text {
  font-size: 14px;
  color: #b2b2b2;
  font-weight: 700;
  line-height: 26px;
}
.page-footer__text a {
  color: #2856b3;
  letter-spacing: .5px;
}
.page-footer__text a:hover {
  color: #8013c5;
  transition: ease-out 0.25s;
}
